// import videoIntroWEBM from "videos/intro-video.webm"
import videoIntroMP4 from "videos/intro-video.mp4"
import ReactPlayer from 'react-player'

const VideoIntro = () => {
	return (
		<div>
			<ReactPlayer playing autoPlay loop muted preload="auto" controls width='100%'
				height='100%' url={process.env.PUBLIC_URL + "/videos/intro-video.webm"} config={{
					file: {
						forceVideo: true
					}
				}}

			>

			</ReactPlayer>
		</div>
		
	)
}

export default VideoIntro;